.photos {
  padding-top: 10px;

  @media (min-width: $tablet-width) {
    padding: 12px $tablet-padding 0;
  }

  @media (min-width: $desktop-width) {
    padding: 19px 0 0;
  }
}

.photos__list {
  @include no-list;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.photos__item {
  padding: 0 $mobile-padding 17px;

  border-top: 1px solid #e5e5e5;

  @media (min-width: $tablet-width) {
    padding: 0;
    width: 50%;

    border: none;

    &--first {
      width: 100%;

      .photos__image {
        width: 648px; //  
        height: 400px; //  
      }
    }

    img {
      width: 324px; //  
      height: 267px; //  
    }
  }

  @media (min-width: $desktop-width) {
    position: relative;

    width: 33%;
    flex-grow: 1;

    &--first {
      width: 100%;

      .photos__image {
        width: 1200px; //  
        height: 400px; //  
      }
    }

    img {
      width: 400px; //  
      height: 267px; //  
    }
  }
}

.photos__image {
  display: block;
  width: 280px;
  padding-top: 22px;

  @media (min-width: $tablet-width) {


    padding-top: 0;
  }
}

.photos__caption {
  padding-top: 15px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 30px;
    padding-bottom: 17px;

    background-color: #f2f2f2;

    &--first {
      padding-right: 20px;
    }
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    bottom: 0;
    width: 100%;

    background-color: rgba(0, 0, 0, 0.3);
  }
}

.photos__title {
  margin: 0;

  font-size: 14px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;

  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}

.photos__author {
  margin: 0;
  margin-bottom: 8px;

  text-transform: none;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}

.photos__likes {
  display: flex;
  margin: 0;

  svg {
    margin-right: 15px;
    fill: #b2b2b2;
  }

  @media (min-width: $tablet-width) {
    svg {
      margin-right: 10px;
      fill: #a9a9a9;
    }
  }

  @media (min-width: $desktop-width) {
    svg {
      fill: #ffffff;
    }
  }
}

.photos__likes-number {
  font-weight: bold;
  color: #000000;

  @media (min-width: $tablet-width) {
    &::before {
      top: -2px;
      left: 5px;
    }
  }

  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}
